<template>
  <div>
    <!-- 头部信息 -->
    <header>
      <div class="article">
        <div class="yuan"></div>
        <p>小小酥</p>
      </div>
    </header>
    <!-- 信息栏 -->
    <div class="bar">
      <div class="contariner">
        <!-- 空白 -->
        <div class="empty"></div>
        <!-- 资料 -->
        <div class="datum">
          <div class="line fl-ex ju-be al-it">
            <p>消息通知</p>
            <img src="../../../static/img/26.png" />
          </div>
          <div class="line fl-ex ju-be al-it">
            <p>会员中心</p>
            <img src="../../../static/img/26.png" />
          </div>
          <div class="line fl-ex ju-be al-it">
            <p>历史记录</p>
            <img src="../../../static/img/26.png" />
          </div>
          <div class="line1 fl-ex ju-be al-it">
            <p>设置</p>
            <img src="../../../static/img/26.png" />
          </div>
        </div>
        <!-- 关于 -->
        <div class="guanyu">
          <div class="about fl-ex ju-be al-it">
            <p>关于</p>
            <img src="../../../static/img/26.png" />
          </div>
        </div>
      </div>
    </div>
    <!-- 尾部 -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from "../home/footer";
export default {
  data() {
    return {};
  },
  components: {
    Footer
  }
};
</script>

<style scoped>
header {
  width: 100%;
  height: 12rem;
  background: url("../../../static/img/38.png");
  background-size: 100% 12rem;
}
header .article {
  width: 50%;
  height: 12rem;
  margin: 0 auto;
  position: relative;
}
header .article .yuan {
  width: 22%;
  height: 5rem;
  margin: 2.2rem 3.4rem;
  position: fixed;
  border-radius: 50%;
  background-color: white;
}
header .article p {
  padding-top: 70%;
  margin: 0 auto;
  font-size: 1.1rem;
  font-weight: 550;
  color: white;
}
.bar {
  width: 100%;
  height: 25.5rem;
  background-color: #f3f3f3;
}
.bar .contariner {
  width: 100%;
}
.bar .contariner p {
  font-size: 0.88rem;
  color: #666666;
  font-weight:bold;
}
.bar .contariner .empty {
  width: 100%;
  height: 3rem;
  background-color: white;
}
.bar .contariner .datum {
  width: 100%;
  height: 12rem;
  background-color: white;
}
.bar .contariner .datum .line {
  width: 88%;
  height: 3rem;
  margin: 0 auto;
  border-bottom: 0.01rem solid #ececec;
}
.bar .contariner .datum .line img,
.bar .contariner .datum .line1 img {
  width: 0.6rem;
  height: 1rem;
}
.bar .contariner .datum .line1 {
  width: 88%;
  height: 3rem;
  margin: 0 auto;
  background-color: white;
}
.bar .contariner .guanyu {
  width: 100%;
  height: 3rem;
  margin-top: 0.7rem;
  background-color: white;
}
.bar .contariner .guanyu .about {
  width: 88%;
  height: 3rem;
  margin: 0 auto;
}
.bar .guanyu .about img {
  width: 0.6rem;
  height: 1rem;
}
</style>


